.process {
  background-color: #ffffff;
}

.process__wrapper {
  padding: 0 20px 49px;
}

.process__title {
  margin: 0 0 40px 0;
  font-family: @oswald;
  .font-sizes(36px; 40px; 400);
  color: #000000;
}

.process__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  .list-reset();
}

.process__item {
  display: flex;
  align-items: center;
}

.process__icon {
  min-width: 60px;
  min-height: 60px;
  margin-right: 20px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.process__icon--leaf {
  background-image: url("../img/icon/leaf-icon.svg");
}

.process__icon--powder {
  background-image: url("../img/icon/powder-icon.svg");
}

.process__icon--eat {
  background-image: url("../img/icon/eat-icon.svg");
}

.process__icon--clock {
  background-image: url("../img/icon/clock-icon.svg");
}

.process__description {
  margin: 0;
  font-family: @lato;
  .font-sizes(14px; 18px; 400);
  color: #444444;
}
